export enum FontWeights {
    "Thin - 100" = 100,
    "Extra Light - 200" = 200,
    "Light - 300" = 300,
    "Regular - 400" = 400,
    "Medium - 500" = 500,
    "SemiBold - 600" = 600,
    "Bold - 700" = 700,
    "Extra Bold - 800" = 800,
    "Black - 900" = 900,
    "Weight - 1000" = 1000
}
export type FontConfigs = {
    fontSize: string,
    fontStyle: string,
    fontWeight: string,
    fontFamily: string,
    fontColor: string,
    combinedFont: string,
    displayWeight: string;
    fontOptions: Array<FontOptions>
}
export interface IThemeFont {
    fontClassName: string,
    configs: FontConfigs,
    showDelete?: boolean
}
export type FontOptions = { fontFamily: string, fontWeights: Array<string>, italic?: boolean } 
export type ITheme = {
    name: string;
    lightTheme: ThemeOptions,
    darkTheme?: ThemeOptions
}

export type ThemeOptions = {
    colors: Array<IThemeColor>;
    fonts: Array<IThemeFont>;
}

export interface IThemeSettings {
    themes: Array<ITheme>,
    defaultTheme: string,
    themeModelId?:string;
}

export interface IThemeColor {
    name: string,
    allowGradient: boolean,
    color: string
}

